@use 'reset.scss';
@use 'produtos.scss';
@use 'cores.scss';
@use 'variaveis.scss';

@mixin containerPadrao() {
    max-width: 1024px;
    width: 100%;
    margin: 0 auto;
}

body {
    background-color: cores.$corDeFundo;
}

li {
    list-style: none;
}

.container {
    @include containerPadrao()
}

header {
    background-color: cores.$CorPrimaria;
    position: sticky;
    top: 0;
    left: 0;

    .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 16px 0;
    }
}

.menu {
    display: flex;

    li {
        font-weight: bold;
        margin-left: 24px;
    }

    a {
        color: cores.$corDeFundo;
        text-decoration: none;
    }
}

@media screen and (max-width: variaveis.$tablet) {
    .container {
        max-width: 80%;
    }

    .products {
        grid-template-columns: repeat(2, 48.5%);
        column-gap: 3%;
    }
}

@media screen and (max-width: variaveis.$mobile) {
    .products {
        display: block;
    }

    .product-item  {
        margin-bottom: 32px;
    }
}